                      <input type="text" class="form-control" placeholder="Description" aria-label="Description" aria-describedby="basic-addon1"  v-model="savedLabel[index]['description']" v-on:input="changeTextNew(savedLabel[index]['description'],writeCanvasDict['description'],clearCanvasDict['description'],'canvas'+index)">
