<script>
import { jsPDF } from "jspdf";
import CanvasComponent from './Canvas.vue'
import Papa from 'papaparse'
//Tmura ML Font
export default {
  name: 'Etiquette',
  components: {
  CanvasComponent
  },
  data: function(){
    return {
      optionsPagination:{
        theme:"bootstrap4"
      },
      page:1,
      etiquette:{
        labelTheme:"new",
        productName:"",
        reference:"",
        description:"",
        dimension:"",
        quantity:"",
        thirdDescription:"",
        secondDescription:"",
        price:"",
        currency:"€",
        topRightSquare:"",
        bottomRightSquare:"",
        year:"mois-2023",
        mainColor:"#FFFFFF",
        secondColor:"#000000"
      },
      showEditor: true,
      labelCheckbox:{
        labelTheme:true,
        productName:false,
        reference:false,
        description:false,
        dimension:false,
        quantity:false,
        thirdDescription:false,
        secondDescription:false,
        price:false,
        currency:true,
        topRightSquare:false,
        bottomRightSquare:false,
        year:true
      },
      availableCurrencies: ["€","$","£"],
      availableThemes:[{name:"Ancienne Etiquette",value:"old",mainColor:"#F0367D",secondColor:"#FFFFFF"},{name:"Nouvelle Etiquette",value:"new",mainColor:"#FFFFFF",secondColor:"#000000"}],
      pink: "#F0367D",
      fontName:"DinProMedium",
      clearCanvasDict:{
      },
      writeCanvasDict:{
    },
      themesColor:{old:{mainColor:"#F0367D",secondColor:"#FFFFFF"},
                  new:{mainColor:"#112655",secondColor:"#FFFFFF"}},
      savedLabel:[]
    }
  },
  mounted (){
    this.drawCanvasNew("canvas",this.etiquette)
    document.onreadystatechange = () => {
      if (document.readyState == "complete") {

        this.drawAllNew("canvas",this.etiquette)
      }
    }
  },
  methods:{
    async loadImage() {
      return new Promise(()=>{
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      const logo = new Image();

      logo.onload = () => {
          const newWidth = 75; // reduce width by 50%
          const newHeight = 75; // reduce height by 50%
          ctx.drawImage(logo,15,15,newWidth,newHeight);
      };
      logo.src = require("../assets/logoVM.png")
      });
    },
    paginationCallback(page){
      console.log(page+" was called")
    },
    deleteLabel(index){
      console.log(this.savedLabel[index]["productName"])
      this.savedLabel.splice(index,1)
      for(var i in this.savedLabel){
        this.redraw("canvas"+i,i)
      }
    },
    redraw(canvasName,index){
      if(index==-1){
        this.drawCanvasNew(canvasName,this.etiquette)
        this.drawAllNew(canvasName,this.etiquette)
      }
      else{
        this.drawCanvasNew(canvasName,this.savedLabel[index])
        this.drawAllNew(canvasName,this.savedLabel[index])    
      }
    },
    changeText(name,fontData,clear){
      this.clearCanvas(clear)
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = fontData[0]
      ctx.font = fontData[3]
      ctx.textAlign = fontData[4]
      const words = name.split(' ')
      let line = '';
      const maxWidth = 420; // maximum width for one line of text
      const lineHeight = 25; // height of each line of text
      let y = fontData[2]; // initial y position for first line of text
      for (let i = 0; i < words.length; i++) {
        const testLine = line + words[i] + ' ';
        const metrics = ctx.measureText(testLine);
        const testWidth = metrics.width;
        if (testWidth > maxWidth) {
          ctx.fillText(line, fontData[1], y);
          line = words[i] + ' ';
          y += lineHeight;
        }
        else {
          line = testLine;
        }
      }
      ctx.fillText(line, fontData[1],y);
    },
    changeTextNew(name,fontData,clear,canvasID){
      this.clearCanvasNew(canvasID,clear)
      var canvas = document.getElementById(canvasID);
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = fontData[0]
      ctx.font = fontData[3]
      ctx.textAlign = fontData[4]
      const words = name.split(' ')
      let line = '';
      const maxWidth = 420; // maximum width for one line of text
      const lineHeight = 25; // height of each line of text
      let y = fontData[2]; // initial y position for first line of text
      for (let i = 0; i < words.length; i++) {
        const testLine = line + words[i] + ' ';
        const metrics = ctx.measureText(testLine);
        const testWidth = metrics.width;
        if (testWidth > maxWidth) {
          ctx.fillText(line, fontData[1], y);
          line = words[i] + ' ';
          y += lineHeight;
        }
        else {
          line = testLine;
        }
      }
      ctx.fillText(line, fontData[1],y);
    },
    clearCanvas(data){
      var canvas = document.getElementById("canvas");
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = data[0]
      ctx.fillRect(data[1],data[2],data[3],data[4])

    },
    clearCanvasNew(canvasID,data){
      var canvas = document.getElementById(canvasID);
      var ctx = canvas.getContext("2d");
      ctx.fillStyle = data[0]
      ctx.fillRect(data[1],data[2],data[3],data[4])

    },
    renderCanvas() {
      return new Promise(resolve => {
        requestAnimationFrame(() => {
          resolve();
        });
      });
    },
    async saveCanvas(){
      var canvas = document.getElementById("canvas");

      var dataURL
      //var newTab = window.open('about:blank','image from canvas');
      //newTab.document.write("<img src='" + dataURL + "' alt='from canvas'/>");
      const doc = new jsPDF("p", "px", [793.706,1122.52]);
      var x = 10
      var y = 10

      for(const i in this.savedLabel){
        this.etiquette = this.savedLabel[i]
        //this.etiquette["productName"]= i
        console.log(this.etiquette)
        if(i%14==0 && i!=0){
          console.log("newPage")
          doc.addPage()
          y=-135
          
        }
        if(i%2 ==0){
          if(i!=0){
            y+=145
            x-=324
          }
          
        }
        else{
          x+=324
        }
        this.changeCanvas()
        await this.renderCanvas();
        dataURL = canvas.toDataURL("image/PNG");
        doc.addImage(dataURL,"png", x, y,321, 0,'',"slow");
        

      }
      doc.save("a4.pdf")
      //doc.save("a4.pdf");
      //window.open(doc.output('datauristring'));
      this.changeCanvas()
    },
    drawAllNew(canvasID,label){
      for (const data in this.writeCanvasDict){
        this.changeTextNew(label[data],this.writeCanvasDict[data],this.clearCanvasDict[data],canvasID)
      }
    },
    drawAll(){
      for (const data in this.writeCanvasDict){
        this.changeText(this.etiquette[data],this.writeCanvasDict[data],this.clearCanvasDict[data])
      }
    },
