          price:[label["mainColor"],386,226,"bold 80px MyriadPro-Regular","right"],
          topRightSquare:[label["mainColor"],464,184,"bold 20px MyriadPro-Regular","left"],
          bottomRightSquare:[label["mainColor"],464,228,"bold 20px MyriadPro-Regular","left"],
        }
        //#112655
        label["test"] = "#F0367D";
        label["done"] = "#00FFFF";
        this.clearCanvasDict = {
          year:[label["mainColor"],95,0,70,30],
          reference:[label["mainColor"],470,140,180,20],
          description:[label["mainColor"],120,40,420,80],
          productName:[label["mainColor"],250,0,250,35],
          dimension:[label["mainColor"],120,120,420,20],
          quantity:[label["mainColor"],20,130,100,20],
          currency:[label["secondColor"],390,160,60,80],
          price:[label["secondColor"],20,160,372,80],
          topRightSquare:[label["secondColor"],450,166,118,26],
          bottomRightSquare:[label["secondColor"],450,210,118,26],
        }
        canvas.width=588
        canvas.height=260
        ctx.fillStyle = label["mainColor"];
        ctx.fillRect(0,0, canvas.width,canvas.height)
        ctx.fillStyle = label["secondColor"];
        ctx.fillRect(20,160, canvas.width-40,80)
        const logo = new Image();
        logo.src = require("../assets/logoVM.png");
        logo.onload = function (){
          const newWidth = 75; // reduce width by 50%
          const newHeight = 75; // reduce height by 50%
          ctx.drawImage(logo,15,15,newWidth,newHeight);
        }
      }
    },
    drawCanvas(name){
