        ctx.fillRect(0,0, canvas.width,canvas.height)
        ctx.fillStyle = this.etiquette["secondColor"];
        ctx.fillRect(20,160, canvas.width-40,80)
        //this.loadImage();
        var logo = new Image();
        logo.src = require("../assets/logoVM.png");
        logo.onload = function (){
          const newWidth = 75; // reduce width by 50%
          const newHeight = 75; // reduce height by 50%
          ctx.drawImage(logo,15,15,newWidth,newHeight);
        }
      }
    },
    changeCanvas(){
      this.drawCanvas(this.etiquette["labelTheme"])
      this.drawAll();

    },
    nextLabel(){
      this.savedLabel.push(JSON.parse(JSON.stringify(this.etiquette)))
      this.cleanLabel()
      this.changeCanvas()
    },
    cleanLabel(){
      for(var i in this.labelCheckbox){
        if(this.labelCheckbox[i]== false){
          this.etiquette[i]=""
        }
      }
      this.themesColor[this.etiquette["labelTheme"]]["mainColor"] =this.etiquette['mainColor']
      this.themesColor[this.etiquette["labelTheme"]]["secondColor"] =this.etiquette['secondColor']

    },
    drawEditedCanvas(index){
      console.log(index)
      this.drawCanvasNew(this.savedLabel[index],"canvas"+index)
      this.drawAllNew(this.savedLabel[index],"canvas"+index)
    },
    dragover(event) {
      event.preventDefault();
      // Add some visual fluff to show the user can drop its files
      if (!event.currentTarget.classList.contains('bg-green-300')) {
        event.currentTarget.classList.add('bg-green-300');
      }
    },
    dragleave(event) {
      // Clean up
      event.currentTarget.classList.remove('bg-green-300');
    },
    drop(event) {
      event.preventDefault();
      var csvFile = event.dataTransfer.files;
      this.uploadFile(csvFile[0]); // Trigger the onChange event manually
      // Clean up
      event.currentTarget.classList.remove('bg-green-300');
    },
    onSendCSV(event){
      console.log(event.target.files)
      this.uploadFile(event.target.files[0])
      event.target.value = ""
    },
    uploadFile(csvFile){
      if(csvFile.type == "text/csv"){
        Papa.parse(csvFile, {header:true,
                                           complete: (results) =>{console.log("yo");
                                                                 console.log(results["data"])
                                                                 for(var i in results["data"]){
                                                                   if(results["data"][i]["labelTheme"]== "" || results["data"][i]["labelTheme"]== undefined){
                                                                     results["data"][i]["labelTheme"] = this.etiquette["labelTheme"]
                                                                   }
                                                                   if(results["data"][i]["mainColor"]== "" || results["data"][i]["mainColor"]== undefined){
                                                                     results["data"][i]["mainColor"] = this.themesColor[results["data"][i]["labelTheme"]]["mainColor"]
                                                                   }
                                                                   if(results["data"][i]["secondColor"]== "" || results["data"][i]["secondColor"]== undefined){
                                                                     results["data"][i]["secondColor"] = this.themesColor[results["data"][i]["labelTheme"]]["secondColor"]
                                                                   }
                                                                   this.savedLabel.push(results["data"][i])
                                                                 }}})
      }
      else{
        console.log("NOT VALID")
      }
    }
  }
}

